html {
    box-sizing: border-box;
}

body {
    font-family: 'Roboto', sans-serif;
    font-size: 20px;
    line-height: 1;
    font-weight: 400;
    color: #252525;
    position: relative;
    height: 100%;
    background: #fff;
    -webkit-tap-highlight-color: transparent;
}

div,
section,
footer,
.btn {
    box-sizing: border-box;
}

a {
    color: #fff;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
    text-decoration: none;
}

a:hover {
    text-decoration: none;
}

a:focus,
input:focus {
    outline: none;
}

p {
    font-weight: normal;
    font-size: 18px;
    line-height: 176.9%;
    color: #000000;
    margin: 0 0 10px 0;
}

input {
    display: block;
    width: 100%;
    height: 46px;
    padding: 0 18px;
    font-weight: normal;
    font-size: 16px;
    line-height: 100%;
    color: #000000;
    box-shadow: none;
    border: none;
    margin-bottom: 31px;
    background: #FFFFFF;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

input:hover,
input:focus {
    outline: none;
}

ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

ul li {
    padding: 0 0 0 0;
}

button {
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: all 0.1s linear;
    -moz-transition: all 0.1s linear;
    -ms-transition: all 0.1s linear;
    -o-transition: all 0.1s linear;
    transition: all 0.1s linear;
}

.title {
    font-weight: bold;
    font-size: 38px;
    line-height: 45px;
    text-align: center;
    color: #000000;
    margin-bottom: 20px;
}

.desc {
    color: #252525;
    font-size: 20px;
    font-weight: 400;
    line-height: 29px;
    margin-bottom: 15px;
}

img {
    max-width: 100%;
    max-height: 100%;
    display: block;
}

.clearfix:after {
    content: '';
    display: table;
    clear: both;
}

.wrapper {
    position: relative;
}

.container {
    position: relative;
    max-width: 1262px;
    width: 100%;
    padding: 0 16px;
    margin: 0 auto;
}

input:not([type=checkbox]):not([type=radio]),
textarea,
select {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}


/* I E   S T Y L E S */

@media all and (-ms-high-contrast: none),
(-ms-high-contrast: active) {
    .block {
        width: 100%;
    }
}

#hamburger-icon {
    position: relative;
    display: none;
    width: 16px;
    height: 14px;
    top: -1px;
}

#hamburger-icon .line {
    position: absolute;
    left: 0;
    display: block;
    background: #2F943F;
    width: 16px;
    height: 2px;
    transition: .4s;
    -webkit-transition: .4s;
    -moz-transition: .4s;
}

.line.line-1 {
    top: 0;
}

.line.line-3 {
    top: 100%;
}

.line.line-2 {
    top: 50%;
}

#hamburger-icon.active .line-1 {
    transform: translateY(7px) translateX(0) rotate(45deg);
    -webkit-transform: translateY(7px) translateX(0) rotate(45deg);
    -moz-transform: translateY(7px) translateX(0) rotate(45deg);
    -ms-transform: translateY(7px) translateX(0) rotate(45deg);
    -o-transform: translateY(7px) translateX(0) rotate(45deg);
}

#hamburger-icon.active .line-2 {
    opacity: 0;
}

#hamburger-icon.active .line-3 {
    transform: translateY(-7px) translateX(0) rotate(-45deg);
    -webkit-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -moz-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -ms-transform: translateY(-7px) translateX(0) rotate(-45deg);
    -o-transform: translateY(-7px) translateX(0) rotate(-45deg);
}

.tel-mob {
    display: none;
    font-weight: normal;
    font-size: 18px;
    line-height: 21px;
    text-align: right;
    letter-spacing: 0.015em;
    color: #5A5A5A;
    b {
        font-weight: 700;
    }
}

.ov-hidden {
    overflow: hidden;
}

.header {
    padding: 19px 0 0 0;
}

.header-box__top {
    border-bottom: 1px solid #ECECEC;
    padding-bottom: 12px;
}

.footer-box__top {
    padding-bottom: 12px;
}

.header-box__bottom,
.footer-box__bottom {
    padding: 8px 0 8px 0;
}

.header-box__top .container,
.footer-box__top .container,
.header-box__bottom .container,
.footer-box__bottom .container {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header-contacts {
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    a {
        display: block;
        width: fit-content;
        margin-bottom: 7px;
        font-weight: normal;
        font-size: 22px;
        line-height: 26px;
        text-align: right;
        letter-spacing: 0.015em;
        color: #000000;
        i {
            font-weight: 700;
            font-style: normal;
        }
    }
    span {
        font-weight: normal;
        font-size: 12px;
        line-height: 100%;
        text-align: right;
        letter-spacing: 0.03em;
        color: #000000;
    }
}

.menu {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    li {
        margin-right: 39px;
    }
    li:last-child {
        margin-right: 0;
    }
    a {
        border-bottom: 2px solid transparent;
        font-weight: normal;
        font-size: 14px;
        line-height: 100%;
        color: #5A5A5A;
        padding-bottom: 11px;
    }
    a:hover {
        color: #2F943F;
        border-color: #2F943F;
    }
    .active a {
        color: #2F943F;
        border-color: #2F943F;
    }
}

.btn-callback {
    display: block;
    font-weight: bold;
    font-size: 9px;
    line-height: 100%;
    padding: 11px 0 10px 0;
    text-align: center;
    letter-spacing: 0.17em;
    text-transform: uppercase;
    color: #2F943F;
    border: 1px solid #2F943F;
    box-sizing: border-box;
    max-width: 178px;
    width: 100%;
    box-sizing: border-box;
}

.btn-green {
    display: block;
    font-weight: bold;
    font-size: 11px;
    line-height: 13px;
    padding: 15px 0 14px 0;
    text-align: center;
    letter-spacing: 0.135em;
    text-transform: uppercase;
    border: 1px solid transparent;
    color: #FFFFFF;
    background: #2F943F;
    box-sizing: border-box;
}

.top-wrap {
    padding: 203px 0 203px 0;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .btn-green {
        max-width: 240px;
        width: 100%;
    }
}

.slogan {
    max-width: 705px;
    width: 100%;
    background: rgba(255, 255, 255, 0.75);
    padding: 71px 75px 74px 75px;
    p {
        margin-bottom: 26px;
    }
}

.slogan-desc {
    font-weight: 500;
    font-size: 20px;
    line-height: 100%;
    color: #2F943F;
    margin-bottom: 22px;
}

.slogan-title {
    font-weight: 500;
    font-size: 41px;
    line-height: 120%;
    color: #000000;
    margin-bottom: 21px;
}

.about {
    padding: 105px 0 118px 0;
    background: rgba(243, 246, 248, 0.4);
    .title {
        margin-bottom: 32px;
    }
    p {
        max-width: 880px;
        width: 100%;
        margin: 0 auto;
        font-weight: normal;
        font-size: 20px;
        line-height: 170%;
        text-align: center;
        color: #292929;
    }
}

.products {
    padding: 91px 0 20px 0;
    .title {
        margin-bottom: 54px;
    }
}

.products-item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 136px;
    .btn-green {
        max-width: 185px;
    }
}

.products-item__left {
    width: 48.781%;
}

.products-item__right {
    width: 48.781%;
    padding: 0 0 0 76px;
}

.products-item:nth-child(even) {
    flex-direction: row-reverse;
}

.products-item:nth-child(even) .products-item__right {
    padding: 0 0 0 106px;
}

.products-item-title {
    position: relative;
    font-weight: bold;
    font-size: 25px;
    line-height: 100%;
    color: #000000;
    margin-bottom: 37px;
}

.products-item-title::before {
    content: '';
    display: block;
    position: absolute;
    bottom: -19px;
    left: 0;
    width: 68px;
    height: 2px;
    background: #2F943F;
}

.products-item-img {
    max-width: 600px;
    width: 100%;
    position: relative;
    height: 0;
    padding-bottom: 58.5%;
    img {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

.products-item ul {
    margin-bottom: 20px;
    li {
        position: relative;
        font-weight: normal;
        font-size: 15px;
        line-height: 130%;
        color: #292929;
        padding: 0 0 0 12px;
    }
    li:before {
        content: '';
        display: block;
        position: absolute;
        width: 3px;
        height: 3px;
        background: #292929;
        top: 7px;
        left: 0;
        border-radius: 50%;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        -ms-border-radius: 50%;
        -o-border-radius: 50%;
    }
}

.map-wrap {
    position: relative;
    height: 741px;
}

.map {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
}

.map-contacts {
    position: absolute;
    z-index: 5;
    top: 110px;
    left: 16px;
    max-width: 579px;
    width: 100%;
    padding: 75px 75px 66px 75px;
    background: rgba(255, 255, 255, 0.75);
    .title {
        font-weight: bold;
        font-size: 24px;
        text-align: left;
        line-height: 120%;
        color: #000000;
        margin-bottom: 14px;
    }
    b {
        display: block;
        font-weight: bold;
        font-size: 16px;
        line-height: 169.5%;
        color: #000000;
    }
    span {
        display: block;
        font-weight: normal;
        font-size: 14px;
        line-height: 169.5%;
        color: #000000;
        margin-bottom: 11px;
    }
    a {
        display: inline-block;
        font-weight: normal;
        font-size: 14px;
        line-height: 169.5%;
        color: #000000;
        margin-right: 20px;
        margin-bottom: 11px;
    }
    a:last-child {
        margin-right: 0;
    }
}

.footer {
    padding: 20px 0 0 0;
}

.footer-box__bottom .btn-callback {
    max-width: 197px;
    width: 100%;
}

.footer-box {
    margin-bottom: 11px;
}

.copyright {
    .container {
        padding-top: 17px;
        padding-bottom: 18px;
        border-top: 1px solid #D1D1D1;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}

.year {
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    color: #9F9F9F;
}

.copyright__right {
    display: flex;
    align-items: flex-end;
    a {
        display: block;
        font-weight: normal;
        font-size: 14px;
        line-height: 16px;
        color: #9F9F9F;
        margin-right: 67px;
    }
    a:last-child {
        margin-right: 0;
    }
}

.vn {}

.vn-top-wrap {
    padding: 116px 0 76px 0;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    .title {
        font-weight: 500;
        font-size: 39px;
        line-height: 100%;
        text-align: center;
        color: #FFFFFF;
        margin-bottom: 13px;
    }
    .desc {
        font-weight: 300;
        font-size: 28px;
        line-height: 100%;
        text-align: center;
        color: #FFFFFF;
        margin-bottom: 20px;
    }
}

.breadcrumbs {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0 0 0 25px;
    li {
        position: relative;
        margin-right: 30px;
    }
    li:first-child {
        padding-left: 31px;
    }
    li:first-child:before {
        content: '';
        display: block;
        position: absolute;
        left: 0;
        top: 8px;
        width: 21px;
        height: 6px;
        background: url(../img/arr.svg) no-repeat center;
    }
    li:after {
        content: '/';
        position: absolute;
        right: -18px;
        top: 5px;
        font-weight: 300;
        font-size: 14px;
        line-height: 100%;
        color: #FFFFFF;
    }
    li:last-child:after {
        content: none;
    }
    a {
        font-weight: 300;
        font-size: 14px;
        line-height: 100%;
        color: #FFFFFF;
    }
}

.products-wrap {
    padding: 30px 0 20px 0;
}

.products-btns {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 22px;
    padding-bottom: 20px;
    border-bottom: 1px solid #E9E9E9;
    a {
        display: block;
        padding: 13px 15px 14px 15px;
        font-weight: normal;
        font-size: 18px;
        line-height: 100%;
        color: #727272;
    }
    a.active,
    a:hover {
        color: #2F943F;
        background: #EFEFEF;
    }
}

.products-elems {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
}

.products-elems__elem {
    max-width: 390px;
    width: 33%;
    margin-bottom: 80px;
    margin-right: 30px;
}

.products-elems__elem:nth-child(3n) {
    margin-right: 0;
}

.products-elems__elem-img {
    position: relative;
    height: 0;
    padding-bottom: 89.49%;
    margin-bottom: 15px;
    img {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
        filter: grayscale(100%);
        -webkit-filter: grayscale(100%);
        -moz-filter: grayscale(100%);
        -ms-filter: grayscale(100%);
        -o-filter: grayscale(100%);
        filter: url(desaturate.svg#greyscale);
        filter: gray;
        -webkit-filter: grayscale(1);
        transition: all .2s linear;
        -webkit-transition: all .2s linear;
        -moz-transition: all .2s linear;
        -ms-transition: all .2s linear;
        -o-transition: all .2s linear;
    }
}

.products-elems__elem-title {
    font-weight: 500;
    font-size: 20px;
    line-height: 130%;
    color: #000000;
}

.products-elems__elem:hover img {
    filter: grayscale(0%);
    -webkit-filter: grayscale(0%);
    -moz-filter: grayscale(0%);
    -ms-filter: grayscale(0%);
    -o-filter: grayscale(0%);
    -webkit-filter: grayscale(0);
}

.form-wrap {
    padding: 75px 0 75px 0;
    background: #FBFBFB;
}

.form-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.form-box__left {
    width: 46%;
    .title {
        text-align: left;
        font-weight: 500;
        font-size: 44px;
        line-height: 100%;
        color: #000000;
        margin-bottom: 11px;
    }
    .desc {
        font-weight: normal;
        font-size: 20px;
        line-height: 160%;
        color: #000000;
    }
}

.form-box__right {
    width: 54%;
}

.form-callback {
    background: #F2F2F2;
    padding: 56px 75px 48px 75px;
    button {
        display: block;
        max-width: 250px;
        width: 100%;
        border: none;
        cursor: pointer;
        outline: none;
    }
}

.product-card {
    padding: 57px 0 58px 0;
}

.product-card-box {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}

.product-card-box__left {
    width: 48.781%;
}

.product-card-box__right {
    width: 48.75%;
}

.card-title {
    font-weight: 500;
    font-size: 30px;
    line-height: 130%;
    color: #000000;
    margin-bottom: 16px;
}

.card-desc {
    p {
        font-weight: normal;
        font-size: 16px;
        line-height: 164%;
        color: #000000;
    }
}

.product-card-img {
    position: relative;
    height: 0;
    padding-bottom: 79.835%;
    img {
        position: absolute;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center;
    }
}

@media screen and (min-width:992px) {
    .btn-callback:hover {
        background: #2F943F;
        color: #fff;
    }
    .btn-green:hover {
        background: #49B65A;
        border-color: #49B65A;
    }
}

@keyframes go-left-right {
    from {
        left: 22px;
    }
    to {
        left: 45px;
    }
}

@-webkit-keyframes go-left-right {
    from {
        left: 22px;
    }
    to {
        left: 45px;
    }
}

.error {
    color: red;
    animation: go-left-right 0.05s infinite alternate;
    -webkit-animation: go-left-right 0.05s infinite alternate;
    position: relative;
    animation-iteration-count: 4;
    cursor: text;
}

.hamburger-box {
    position: absolute;
    right: 16px;
    top: 5px;
    display: none;
    justify-content: center;
    align-items: center;
    width: 38px;
    height: 38px;
    background: #FFFFFF;
    border: 1px solid #2F943F;
    box-sizing: border-box;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
    z-index: 99;
    transition: all .2s linear;
    -webkit-transition: all .2s linear;
    -moz-transition: all .2s linear;
    -ms-transition: all .2s linear;
    -o-transition: all .2s linear;
}

.hamburger-box.active {
    border-color: transparent;
}

.callback-mob {
    display: none;
    position: absolute;
    top: 5px;
    right: 73px;
    width: 38px;
    height: 38px;
    background-color: #FFFFFF;
    border: 1px solid #2F943F;
    box-sizing: border-box;
    background-image: url(../img/phone.svg);
    background-position: center;
    background-repeat: no-repeat;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}


/* 
    M O B I L E
*/

@media screen and (max-width: 1200px) {
    html,
    body {
        min-width: 320px;
        width: 100%;
        overflow-x: hidden;
    }
    a:focus {
        outline: none;
    }
    .about {
        padding: 70px 0 70px 0;
    }
    .top-wrap {
        padding: 100px 0 80px 0;
    }
    .slogan {
        padding: 50px;
    }
}

@media screen and (max-width: 1100px) {
    .copyright__right a {
        margin-right: 35px;
    }
}

@media screen and (max-width: 991px) {
    .btn-callback:active {
        background: #2F943F;
        color: #fff;
    }
    .header {
        padding: 22px 0 17px 0;
    }
    .header-box__top .container {
        flex-direction: column;
        align-items: flex-start;
    }
    .header .logo {
        width: 128px;
        height: 36px;
        margin-bottom: 19px;
    }
    .header-box__top {
        border: none;
        padding-bottom: 7px;
    }
    .btn-callback {
        max-width: 100%;
        padding: 16px 0 15px 0;
        font-size: 11px;
    }
    #hamburger-icon {
        display: block;
    }
    .header .callback-mob {
        display: block;
    }
    .hamburger-box {
        display: flex;
    }
    .header-contacts a {
        display: none;
    }
    .mobile-menu {
        position: fixed;
        top: 0;
        right: 0;
        width: 79.3vw;
        margin: 0;
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        justify-content: start;
        height: 100vh;
        z-index: 30;
        overflow: auto;
        background: #FFFFFF;
        box-shadow: 0px 4px 43px rgba(0, 0, 0, 0.1);
        padding: 85px 16px 25px 16px;
        box-sizing: border-box;
        transform: translate(100vw, 0);
        -webkit-transform: translate(100vw, 0);
        -moz-transform: translate(100vw, 0);
        -ms-transform: translate(100vw, 0);
        -o-transform: translate(100vw, 0);
    }
    .anim-menu {
        transition: .3s ease-in-out;
        -webkit-transition: .3s ease-in-out;
        -moz-transition: .3s ease-in-out;
        -ms-transition: all .3s ease-in-out;
        -o-transition: .3s ease-in-out;
    }
    .mobile-menu.active {
        transform: translate(0, 0);
        -webkit-transform: translate(0, 0);
        -moz-transform: translate(0, 0);
        -ms-transform: translate(0, 0);
        -o-transform: translate(0, 0);
    }
    .mobile-menu .menu {
        width: 100%;
        display: flex;
